/* You can add global styles to this file, and also import other style files */
@import '@angular/material/prebuilt-themes/deeppurple-amber.css';
@import url("https://fonts.googleapis.com/css?family=Nunito&display=swap");
@import '~perfect-scrollbar/css/perfect-scrollbar.css';
.btn-primary {
  background-color: #2D86F7;
  color: white;
  font-weight: 200;
}

.a-primary {
  color: #2D86F7;
}

.nav-primary {
  background-color: #EFBF19;
  color: white;
}

.color-back {
  background-color: #ECEFF0;
}

h2 {
  color: #424242;
}

.btn-second {
  font-size: 1em;
  color: white;
  background-color: #0cb39c;
}
.btn-second:hover {
  background-color: #00a8a8;
}

.mat-footer-row, .mat-header-row, .mat-row {
  width: 100% !important;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  font-family: Nunito, "Helvetica Neue", sans-serif;
}
* a {
  text-decoration: none;
  cursor: pointer;
}

html, body {
  height: 100%;
}

body {
  margin: 0;
  font-family: Nunito, "Helvetica Neue", sans-serif;
}

.mat-dialog-content .mat-horizontal-stepper-header-container {
  display: none !important;
}

.mat-dialog-container {
  padding: 0px !important;
  margin: 0px !important;
}

.mat-checkbox-indeterminate.mat-accent .mat-checkbox-background, .mat-checkbox-checked.mat-accent .mat-checkbox-background {
  background-color: #2D86F7;
}

.mat-ripple-element {
  color: #2D86F7;
}

.mat-radio-button.mat-accent.mat-radio-checked .mat-radio-outer-circle {
  border-color: #2D86F7;
}

.mat-radio-button.mat-accent .mat-radio-inner-circle, .mat-radio-button.mat-accent .mat-radio-ripple .mat-ripple-element:not(.mat-radio-persistent-ripple), .mat-radio-button.mat-accent.mat-radio-checked .mat-radio-persistent-ripple, .mat-radio-button.mat-accent:active .mat-radio-persistent-ripple {
  background-color: #2D86F7;
}

.mat-primary .mat-option.mat-selected:not(.mat-option-disabled) {
  color: #2D86F7;
}

.mat-primary .mat-option.mat-selected:not(.mat-option-disabled) {
  color: #2D86F7;
}

.mat-form-field.mat-focused .mat-form-field-ripple {
  background-color: #0cb39c;
}

.mat-form-field.mat-focused .mat-form-field-ripple {
  background-color: #0cb39c;
}

.mat-card-header-text {
  width: 100% !important;
  margin: 0 !important;
}

.center {
  display: grid;
  justify-items: center;
}

body {
  background-color: #ECEFF0;
}

button {
  border: none;
  background-color: transparent;
}

.container {
  width: 100%;
}

footer-component .footer {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  background-color: #2D86F7;
  color: white;
  height: 300px;
  bottom: 0px;
  margin-top: 3rem;
}
footer-component .footer div {
  padding: 3rem;
}
footer-component .footer div p {
  margin-top: 1rem;
  font-weight: 300;
}

.toastSuccess {
  background-color: #42a847;
}

.toastWarn {
  background-color: #bc5100;
}

.toastError {
  background-color: #d50000;
}

.acciones button {
  float: left;
}
.acciones .ico-edit {
  color: #bc5100;
  float: left;
}

td.mat-cell:last-of-type, td.mat-footer-cell:last-of-type, th.mat-header-cell:last-of-type {
  padding-right: 0 !important;
}

gallery-core[thumbPosition=bottom] gallery-thumbs .g-slider, gallery-core[thumbPosition=top] gallery-thumbs .g-slider {
  left: 0% !important;
  width: 100%;
  padding-left: 10%;
  margin-top: 0.5rem;
}